:root {
    --Light-Cyan-Cabecera: #14B38B;
    --Light-Cyan-aside1: #188F71;
    --Light-Cyan-articulo: #14B38B;
    --Light-Cyan-aside2: #188F71;
    --Light-Cyan-bkground: #90CCBE;
    --Light-Cyan-bxsh: #0b4b3c;
    --Light-Cyan-texto: #031410;

    --Light-Roxa-Cabecera: #A30046;
    --Light-Roxa-aside1: #730034;
    --Light-Roxa-articulo: #CB0057;
    --Light-Roxa-aside2: #CB0057;
    --Light-Roxa-bkground: #FA026D;
    --Light-Roxa-bxsh: #3f0b22;
    --Light-Roxa-texto: #32091c;

    --Dark-Cyan-Cabecera: #164136;
    --Dark-Cyan-aside1: #1c4f42;
    --Dark-Cyan-articulo: #12604d;
    --Dark-Cyan-aside2: #094637;
    --Dark-Cyan-bkground: #18723c;
    --Dark-Cyan-bxsh: #081713;
    --Dark-Cyan-texto: #b7f2e4;

    --Dark-Roxa-Cabecera: #641638;
    --Dark-Roxa-aside1: #4c132d;
    --Dark-Roxa-articulo: #5a1633;
    --Dark-Roxa-aside2: #431c2d;
    --Dark-Roxa-bkground: #860e1c;
    --Dark-Roxa-bxsh: #3f0b22;
    --Dark-Roxa-texto: #f7d6e5;
}

header#principal{
    background-color: #2f0057;
    z-index: 20 !important;
    box-shadow: 1px 1px 3px #031410 !important;
}
main{
    background-color: #ba1cd2;
}
div#relleno{
    background-color: #ba1cd2;
}
footer{
    background-color: #2f0057;
}
nav#focusIndex{
    background-color: #2f0057;
}
nav#focusTools{
    background-color: #2f0057;
}

abbr{
    text-decoration: none;
}

/*
***************

******************

**********************************

******************

**************
*/
[cor=Light--Cyan] main{
    background-color: var(--Light-Cyan-bkground);
}
[cor=Light--Cyan] div#relleno{
    background-color: var(--Light-Cyan-bkground);
}
[cor=Light--Cyan] header#principal{
    background-color: var(--Light-Cyan-Cabecera);
}

[cor=Light--Cyan] input{
    background-color: var(--Light-Cyan-texto);
}
[cor=Light--Cyan] input:focus{
    background-color: var(--Light-Cyan-bxsh);
}

[cor=Light--Cyan] nav#focusIndex{
    background-color: var(--Light-Cyan-aside1);
}
[cor=Light--Cyan] nav#focusTools li{
    color: var(--Light-Cyan-texto);
}
/*
>>>**¨¨Button
*/
[cor=Light--Cyan] button{
    background-color: var(--Light-Cyan-bkground);
}
[cor=Light--Cyan] button:focus{
    background-color: var(--Light-Cyan-Cabecera) !important;
    border-radius: 1em;
}
[cor=Light--Cyan] button:hover{
    background-color: var(--Light-Cyan-aside1);
}
/*
<<<**¨¨Button
*/

[cor=Light--Cyan] nav#focusTools{
    background-color: var(--Light-Cyan-aside2);
}

[cor=Light--Cyan] article#ArtCprincipal{
    color: var(--Light-Cyan-texto);
    background-color: var(--Light-Cyan-articulo);
}

[cor=Light--Cyan] div.ObjetoIndividual>span>h3{
    color: #500b56;
}

[cor=Light--Cyan] footer{
    background-color: var(--Light-Cyan-bxsh);
}

[cor=Light--Cyan] footer>ul>li>a:hover {
    background-color: var(--Light-Cyan-aside1);
}

