footer li{
    list-style: none;
}

footer{
    border: 2vh;
    position: relative;
    display: flex;
    width: 100.2%;
    height: 14vh;

    top: 11vh;
    z-index: 1;
}

footer>ul{
    position: relative;
    display: flex;

    height: 100%;
    width: 33%;
    
    flex-grow: 1;
    flex-direction: column;
}

footer>ul>li{
    position: relative;
    display: flex;
    flex-grow: 1;
    box-shadow: rgba(0, 0, 0, 0.2) 1px 1px 2px 2px;
}
footer>ul>li>a{
    outline: none;
    text-decoration: none;
    margin: auto;
    color: rgb(123, 247, 247);
    
    align-items: center;
}

footer>ul>li>a:link{
    color: white;
}
footer>ul>li>a:visited{
    color: rgb(56, 203, 203);
}