#principal{
    top: 0;
    position: fixed;
    width: 100%;
    height: 12vh;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    z-index: 10;
}

#principal>nav{
    position: relative;
    margin-left: .4em;
    margin-right: .4em;
    min-width: 8vh;
}

#principal>nav#index{
    height: 98%;
    width: 20%;
}

#principal>nav#search{
    margin-top: 2vh;
    height: 70%;
    width: 20%;
    flex-grow: 2;
}

#principal>nav#search>input{
    position: relative;
    padding: .2vh;
    margin: 2vh;
    width: 84%;
    height: 6vh;
    font-size: 3vh;
}

#principal>nav#tools{
    position: relative;
    height: 98%;
    flex-grow: 1.2;
}

#principal button{
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    cursor: alias;
    border-color: transparent;
    z-index: 2;
    background-color: transparent;
}

#principal img{
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: fill;
}
#principal>nav#index>button{
    width: 134%;
}

#principal>nav#index img{
    width: 14vh;
    height: 12vh;
    left: -.4em;
}

#principal>nav#tools img{
    right: -.8em;
    width: 14vh;
    height: 10vh;
}

nav#focusIndex abbr{
    margin-left: 1em;
    margin-right: .2em;
}

/*pc*/
@media only screen and (min-width: 667px){
    #principal>nav{
        margin-left: 1.5em;
        margin-right: 1.5em;
    }

    #principal>nav#index img{
        width: 18vh !important;
        height: 14vh;
    }

    #principal>nav#tools img{
        margin: auto;
        width: 16vh;
        height: 12vh;
        right: 0;
    }
}

nav#focusTools>ul>li#liAlmacen{
    flex-grow: 2;
}
nav#focusTools>ul>li#liAlmacen>button{
    width: 100%;
    height: 100%;
    flex-grow: 1;
}
nav#focusTools>ul>li#liSize{
    padding: .4em;
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex-grow: 2;
}
nav#focusTools>ul>li#liSize>label{
    top: 0;
    height: 1vh;
    margin: auto;
}
nav#focusTools>ul>li>button#BtRestore{
    width: 100%;
    height: 5vh;
}

button#counterfocus{
    position: fixed;
    z-index: 9;
    left: 0;
    top: 0;
    transition: all;
}
button#counterfocus[fc="False"]{
    width: 2em;
    height: 2em;
    background-color: rgb(82, 8, 8);
    opacity: .6;
}
button#counterfocus[fc="CounterFocus"]{
    width: 100%;
    height: 100%;
    background-color: black;
    opacity: .2;
}