* {
    transition: 2s all;
}

input {
    transition: 0s all;
}

input:focus {
    outline: transparent;
}

nav[frt='X'] {
    -webkit-box-shadow: 0px 0px 39px 4px rgba(0, 0, 0, 0.64);
    -moz-box-shadow: 0px 0px 39px 4px rgba(0, 0, 0, 0.64);
    box-shadow: 0px 0px 39px 4px rgba(0, 0, 0, 0.64);
}

ul#CheckCor>form input {
    clip-path: polygon(50% 0%, 61% 35%, 98% 35%, 68% 57%, 79% 91%, 50% 70%, 21% 91%, 32% 57%, 2% 35%, 39% 35%);
    -webkit-appearance: none;
    appearance: none;
}
ul#CheckCor>form input:checked{
    background-color: yellow;
    border-color: darkgoldenrod;
}
/*pc*/
@media only screen and (min-width: 667px){
    ul#CheckCor input{
        left: 5em;
    }
}