*{
    margin: 0;
    padding: 0;
}
main{
    position: relative;
    top: 12vh;
    width: 100%;
    height: 100%;
}
main *{
    overflow: hidden;
    overflow-wrap: break-word;
}

article#ArtCprincipal{
    position: relative;
    display: flex;
    flex-direction: column;

    min-height: 70vh;
    min-width: 20vh;

    max-width: 100%;
    width: 80%;
    height: auto;
    
    top: 2vh;
    bottom: 4em;
    margin: auto;
    
    padding: 1em;
    z-index: 2;

    border-radius: 2em;
}
/*pc*/
@media only screen and (min-width: 667px){
    article#ArtCprincipal{
        width: 90%;
    }
}

article#ArtCprincipal div.imgdiv{
    padding-top: .4vh;
    padding-bottom: .4vh;
    margin-top: 1em;
    margin-bottom: 2em;
    border-radius: 1em;
    
    position: relative;
    display: flex;

    width: 100%;
    min-height: 4em;

    overflow: hidden;
}
article#ArtCprincipal div.imgdiv>img{
    position: relative;

    min-width: 6em;
    max-width: 80%;

    min-height: 4em;
    max-height: 24em;

    border-radius: 2em;
    object-fit: contain;
    margin: auto;
}

div#relleno{
    position: relative;
    width: 100%;
    height: 5vh;
}

button#AlmacenMenos{
    width: 4em;
    height: 4em;
    border-radius: 50%;
}


/*
**************************POR ARTICULO
*/

div.ObjetoIndividual{
    position: relative;
    display: flex;
    height: auto;
    padding-top: 2em;
    padding-bottom: 2em;

    flex-direction: column;
    margin: 1em;
    
}
div.ObjetoIndividual *{
    position: relative;
    display: flex;
    justify-content: center;
    justify-items: center;
}
div.ObjetoIndividual>span{
    width: 100%;
    display: flex;

    flex-wrap: wrap;
}
div.ObjetoIndividual>span>h3{
    margin-left: 6px;
    margin-right: 6px;
    padding: .5em;

    justify-items: center;
}

div.tObj{
    position: relative;
    display: flex;

    flex-direction: row;
    
    width: 100%;
    height: auto;
    min-height: 2em;
}

div.tObj>button{
    border-radius: 2em;
    margin: 1em;
    min-height: 3em;
    flex-grow: 1;
}