.fhead{
    position: fixed;
    top: 12vh;
    overflow: hidden;
    z-index: 10;
}
.fhead>ul{
    overflow: hidden;
}
.fhead li{
    list-style: none;
}

nav#focusIndex div.SelTypePlant button{
    border: 0;
}

nav#focusTools[fc='unfocus']{
    width: 100%;
    height: 0;
}
nav#focusIndex[fc='unfocus']{
    width: 0;
    height: 100%;
}

div.SelTypePlant{
    position: absolute;
    width: 0;
    height: auto;
    overflow: hidden;
}

div.SelTypePlant[fc='Yesfc']{
    position: relative;
    width: auto;
}
div.SelTypePlant[fc='Yesfc']>button{
    width: 100%;
    font-weight:300;
}

/*
****INDEX
*/

nav#focusIndex{
    width: 28vh;
    height: 100%;
    flex-direction: column;
    justify-content: center;
    z-index: 11;
}
nav#focusIndex>div{
    height: 1em;
}

nav#focusIndex>ul{
    display: flex;
    width: 100%;
    height: auto;

    flex-direction: column;
    align-items: center;
}
nav#focusIndex>ul>li{
    position: relative;
    display: flex;
    width: 100%;
    height: 8vh;
    margin: .4vh;
    
    box-shadow: rgba(111, 21, 21, 0.2) 1px 1px 6px 2px;
}
nav#focusIndex>ul>li>button{
    position: relative;

    width: 90%;
    height: 90%;


    margin: auto;
    justify-items: center;
    background-color: transparent;
    border: none;
}
nav#focusIndex li.Plants{
    position: relative;
    display: flex;

    
    flex-direction: column;
    align-items: center;
    height: auto;
}
nav#focusIndex li.Plants>button{
    position: relative;
    margin: auto;
    width: 100%;
    height: 8vh;
}

/*
****Tools
*/

nav#focusTools{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 50vh;
    align-items: center;
}

nav#focusTools>ul{
    position: relative;
    display: flex;
    
    flex-direction: column;
    
    padding: 1vh;
    margin: .8em;
    min-width: 96%;
    outline: 1px dashed black;
    flex-grow: 1;
}
nav#focusTools>ul#Other{
    flex-grow: 2;
}
nav#focusTools>ul>li>button{
    transform: scale(1.005);
    border: 0;
    box-shadow: 1px 1px 3px black;
    background-color: red;
    opacity: .7;
}

ul#CheckCor{
    height: 12.5vh;
    max-height: 33%;
}

ul#CheckCor>form{
    position: relative;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;

    width: 100%;
    height: 100%;
}


ul#CheckCor>form>div{
    position: relative;
    display: flex;
    
    width: 100%;
    max-height: 50%;
    
    flex-direction: row;
    flex-wrap: nowrap;

    flex-grow: 1;
}
ul#CheckCor>form>div>li{
    height: 100%;
    flex-grow: 1;
}
ul#CheckCor>form>div>li>label{
    margin: auto;

    font-weight: bold;
    min-width: 70%;

    flex-grow: 1;
}
ul#CheckCor>form>div>li>input{
    height: 2em;
    width: 2em;
    
    z-index: -1;
}
/*pc*/
@media only screen and (min-width: 667px){
    ul#CheckCor{
        max-width: 25%;
    }

    ul#CheckCor>form>div{
        margin: .4em;
    }
    ul#CheckCor>form>div>li{
        margin-right: 1em;
        width: 100%;
    }
    ul#CheckCor>form>div>li>input{
        left: 0;
        right: 0;
        bottom: 0;
        margin: auto;
    }
}