/*
** fsizes={1}
**
** [fsizes="1"]
*/

button[bx='Sh']{
    border-radius: 10%;
    box-shadow: 1px 1px 5px black;
}

/*
***********
*************

****GENERAL

*************
***********
*/

h1{
    font-family: 'Dancing Script', cursive;
}
h2, h3,p{
    font-family: 'Satisfy', cursive;
}
span{
    font-family: 'Dancing Script', cursive;
}

nav button{
    font-family: Georgia, serif;
    /*font-weight: bold;*/
}

/*
***********
*************

****HEADER-index

*************
***********
*/
[fsizes="1"] nav button{
    font-size: 2.2vh;
}
[fsizes="2"] nav button{
    font-size: 2.6vh;
}
[fsizes="3"] nav button{
    font-size: 3vh;
}
[fsizes="4"] nav button{
    font-size: 3.2vh;
}
[fsizes="5"] nav button{
    font-size: 3.6vh;
}

/*
***********
*************

****HEADER-tools

*************
***********
*/
[fsizes="1"] ul#CheckCor>form>div>li>label{
    font-size: 1em;
}
[fsizes="2"] ul#CheckCor>form>div>li>label{
    font-size: 1.2em;
}
[fsizes="3"] ul#CheckCor>form>div>li>label{
    font-size: 1.4em;
}
[fsizes="4"] ul#CheckCor>form>div>li>label{
    font-size: 2em;
}
[fsizes="5"] ul#CheckCor>form>div>li>label{
    font-size: 2.4em;
}

[fsizes="1"] div.SelTypePlant[fc='Yesfc']>button{
    font-size: 2vh;
}
[fsizes="2"] div.SelTypePlant[fc='Yesfc']>button{
    font-size: 2.4vh;
}
[fsizes="3"] div.SelTypePlant[fc='Yesfc']>button{
    font-size: 2.8vh;
}
[fsizes="4"] div.SelTypePlant[fc='Yesfc']>button{
    font-size: 3vh;
}
[fsizes="5"] div.SelTypePlant[fc='Yesfc']>button{
    font-size: 3.4vh;
}

/*pc*/
@media only screen and (min-width: 667px){
    ul#CheckCor[fsizes="1"]>form>div>li>label{
        font-size: 1em;
    }
    ul#CheckCor[fsizes="2"]>form>div>li>label{
        font-size: 1.4em;
    }
    ul#CheckCor[fsizes="3"]>form>div>li>label{
        font-size: 1.8em;
    }
    ul#CheckCor[fsizes="4"]>form>div>li>label{
        font-size: 2em;
    }
    ul#CheckCor[fsizes="5"]>form>div>li>label{
        font-size: 2.4em;
    }
}

/*
***********
*************

****Body

*************
***********
*/

[fsizes="1"] article#ArtCprincipal h1{
    font-size: 6vh;
}
[fsizes="2"] article#ArtCprincipal h1{
    font-size: 6.4vh;
}
[fsizes="3"] article#ArtCprincipal h1{
    font-size: 6.8vh;
}
[fsizes="4"] article#ArtCprincipal h1{
    font-size: 7vh;
}
[fsizes="5"] article#ArtCprincipal h1{
    font-size: 7.4vh;
}

[fsizes="1"] article#ArtCprincipal * {
    font-size: 2.4vh;
}
[fsizes="2"] article#ArtCprincipal * {
    font-size: 2.68vh;
}
[fsizes="3"] article#ArtCprincipal * {
    font-size: 3vh;
}
[fsizes="4"] article#ArtCprincipal * {
    font-size: 3.4vh;
}
[fsizes="5"] article#ArtCprincipal * {
    font-size: 3.80vh;
}


[fsizes="5"] span>h3{
    font-size: 2.4vh;
}
[fsizes="5"] span>h3{
    font-size: 2.8vh,
}
[fsizes="5"] span>h3{
    font-size: 3.2vh,
}
[fsizes="5"] span>h3{
    font-size: 3.6vh;
}
[fsizes="5"] span>h3{
    font-size: 3.4vh,
};

/*
***********
*************

****footer

*************
***********
*/
[fsizes="1"] footer>ul>li>a {
    font-size: 2vh;
}
[fsizes="2"] footer>ul>li>a {
    font-size: 2.4vh;
}
[fsizes="3"] footer>ul>li>a {
    font-size: 2.8vh;
}
[fsizes="4"] footer>ul>li>a {
    font-size: 3vh;
}
[fsizes="5"] footer>ul>li>a {
    font-size: 3.4vh;
}